import React from 'react'
import styled from 'styled-components'

const Box = styled.div`
    width: 100%;
    height: 50px;
    margin-top: 20px;
    background: #ee5253;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
`

const AlertBox = props => (
    <Box>
        {props.errorMessage}
    </Box>
);

export default AlertBox


